.info-container {
    font-family: Arial, sans-serif;
    color: #333;
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #dfdede;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.info-container h2, .info-container h1 {
    font-size: 24px;
    color: #007bff;
    margin-bottom: 10px;
}

.info-container p {
    font-size: 16px;
    margin-bottom: 20px;
}

.info-container ul {
    list-style-type: disc;
    padding-left: 20px;
}

.info-container li {
    margin-bottom: 10px;
    line-height: 1.6;
}
